.HomePage {
  position: relative;
  text-align: center;
  background: white;
  margin-top: 100px;
  width: 100%;
  height: 100%;
}

.MyGalleries{
  position: relative;
  text-align: center;
  background: white;
  margin-top: 100px;
  width: 100%;
  height: 100%;
}

.Channels{
  position: fixed;
  margin-top: 100px;
  text-align: center;
  background: lightpink;
  width: 100%;
  height: 100%;
}

.image-feed {
  width: 50%;
  margin: auto;
}

.image {
  width: 100%;
  text-align: left;
  margin: auto;
}

.float-button {
  -webkit-box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12) !important;
          box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12) !important;
  -webkit-transition: -webkit-box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1) !important;
  transition: -webkit-box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1) !important;
  -o-transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1) !important;
  transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1) !important;
  transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1) !important;
}

.float-button:hover {
  -webkit-box-shadow: 0px 4px 8px -1px rgba(0, 0, 0, 0.2), 0px 3px 12px 1px rgba(0, 0, 0, 0.14), 0px 2px 6px 1px rgba(0, 0, 0, 0.12) !important;
          box-shadow: 0px 4px 8px -1px rgba(0, 0, 0, 0.2), 0px 3px 12px 1px rgba(0, 0, 0, 0.14), 0px 2px 6px 1px rgba(0, 0, 0, 0.12) !important;
}


